import Vue from 'vue'
import Vuex from 'vuex'

import router from './router'


const baseURL = 'https://api.irl.technology'

const postObj = {
	method: 'POST',
	mode: "cors",
	headers: {
		"Content-Type": "application/json",
	}
}

Vue.use(Vuex)
/*	Stock notFetch
notFetch(`auth/${val}`)
*/

async function notFetch(rt = '', cfg = {}) {
	return await fetch(`${baseURL}/${rt}`, cfg)
	.then(res => {
		if (res.status == 401) {
			router.push('/login')
			throw new Error(res.status)
		}
		return res.json()
	})
	.catch(err => {
		console.log(err)
		return err
	})
}

export default new Vuex.Store({
  state: {
		// TODO	remove testing true value
		// isAuthed: true,
		isAuthed: false,
		myUser: {full_name: ''},
		myOrg: {provider_name: ''},
		allOrgs: [],
		inventory: 0,
		referrals: [],
		classes: [],
		// uniqueEligibilities: [],
		uniqueEligibilities: [
			{
				elig_item_type_label: 'first item',
				elig_item_type_code: 1
			},
			{
				elig_item_type_label: 'second item',
				elig_item_type_code: 2
			},
			{
				elig_item_type_label: 'third item',
				elig_item_type_code: 3
			},
		],
		allEligibilities: {},
		myEligibilities: {}
  },
  mutations: {
		updateAuth: (state, auth) => (state.isAuthed = auth),
		updateUser: (state, user) => (state.myUser = user),
		updateAllOrgs: (state, orgs) => (state.allOrgs = orgs),
		updateInv: (state, inv) => (state.inventory = inv),
		updateRefer: (state, refer) => (state.referrals = refer),
		updateClasses: (state, classes) => (state.classes = classes),
		updateAllElig: (state, elig) => (state.allEligibilities = elig),
		updateMyElig: (state, elig) => (state.myEligibilities = elig),

  },
  actions: {
		forceLogin() {
			router.push('/login')
		},
		doLogin({ commit, dispatch }, body) {
			postObj.body = JSON.stringify(body)

			notFetch(`auth`, postObj)
			.then(res => {
				console.dir(res)
				if (res.msg == 'Authentication Successful') {
					//TODO	switch storage
					localStorage.didAuth = true
					// sessionStorage.didAuth = true
					commit('updateAuth', true)
					localStorage.myID = res.data
					// sessionStorage.myID = res.data
					dispatch('loadUser', res.data)
				} else {
					let errString = ''
					for (const error of res.errors) {
						errString += `${error.param}: ${error.msg}\n`
					}
					alert(errString)
				}

			})
			.catch(err => {
				alert("Request Error")
				console.log(err)
			})
		},
		async loadUser({ commit }, userid) {
			userid = userid || localStorage.myID
			console.log("hit loadUser")
			await notFetch(`users/one/${userid}`)
			.then(res => {
				commit('updateUser', res.data)
			})
			return
		},
		loadMyOrg({ commit, state }) {
			notFetch(`provider/${state.myUser.member_of}`)
			.then(res => {
				commit('updateMyOrg', res.data)
			})
		},
		
	},
	getters: {
		checkAuthed: state => state.isAuthed,
		checkUser: state => state.myUser,
		checkMyOrg: state => state.myOrg,
		checkAllOrgs: state => state.allOrgs,
		checkInventory: state => state.inventory,
		checkReferrals: state => state.referrals,
		checkClasses: state => state.classes,
		checkUniqueElig: state => state.uniqueEligibilities,
		checkAllElig: state => state.allEligibilities,
		checkMyElig: state => state.myEligibilities
	}
})
