import Vue from 'vue'
import App from './App.vue'
import store from './store'
import router from './router'

Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
	// if (sessionStorage.didAuth /*&& localStorage.name*/) {
	// 	// this.name = localStorage.name;
	// 	console.log(sessionStorage.didAuth)
	// }
	// if (sessionStorage /*&& localStorage.name*/) {
	// 	// this.name = localStorage.name;
	// 	console.log('sessionStorage accessible!')
	// }

  if (to.matched.some(record => record.meta.requiresAuth)) {
    // if (!store.getters.checkAuthed && sessionStorage.didAuth != 'true') {
		if (!store.getters.checkAuthed && localStorage.didAuth != 'true') {
      next({ name: 'login' })
    // } else if (!store.getters.checkUser && sessionStorage.myUser) {
		} 
		if (!store.getters.checkUser.member_of) {
			store.dispatch('loadUser')
			.then(() => {store.dispatch('loadMyOrg')})
			next()
		}
		if (!store.getters.checkMyOrg.provider_id) {
			store.dispatch('loadMyOrg')
		}
  }
	next()
})

new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app')
