import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Eligibility from './views/Eligibility.vue'
import Referrals from './views/Referrals.vue'
import Classes from './views/Classes.vue'
import Inventory from './views/Inventory.vue'
import Manage from './views/Manage.vue'
// import Eligibility from './views/Eligibility.vue'
// import Eligibility from './views/Eligibility.vue'

import Login from './views/Login.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
			meta: {
				requiresAuth: true
			}
    },
		{
      path: '/elig',
      name: 'elig',
			component: Eligibility,
			meta: {
				requiresAuth: true
			}
    },
		{
      path: '/refer',
      name: 'refer',
      component: Referrals,
			meta: {
				requiresAuth: true
			}
    },
		{
      path: '/classes',
      name: 'classes',
      component: Classes,
			meta: {
				requiresAuth: true
			}
    },
		{
      path: '/inventory',
      name: 'inventory',
      component: Inventory,
			meta: {
				requiresAuth: true
			}
    },
		{
      path: '/manage',
      name: 'manage',
      component: Manage,
			meta: {
				requiresAuth: true
			}
    },
		{
      path: '/login',
      name: 'login',
      component: Login
    }
  ]
})

