<template>
  <div class="home">
		<!-- <p>Name: {{ checkUser.full_name }}</p>
		<p>Member Of: {{ checkMyOrg.provider_name }}</p> -->
		<p>Home</p>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex"

export default {
	
  name: 'home',
	components: {
    // none
	},
	data() {
		return {
			defaultOrg: "ACME Corp",
			defaultUser: "Joe Average"
		}
	},
	methods: {
		...mapActions(["loadMyOrg", "loadUser"]),
		
	},
	created() {
		// console.dir(this.checkUser)
		if (!this.checkUser.full_name) {
			this.loadUser()
			.then( _ => this.loadMyOrg())
		}
  },
	computed: mapGetters(["checkUser", "checkMyOrg"]),
}
</script>
